<template>
  <div class="footer">
    <div class="footer_info">
      <div class="info_item">
        <div class="item_tile"></div>
        <ul>
          <li class="item_content" v-for="(item, i) of navList" :key="i">
            {{ item }}
          </li>
        </ul>
      </div>
      <div class="info_item">
        <div class="item_tile"></div>
        <ul>
          <li
            class="item_content"
            v-for="(item, i) of persionInfo"
            :key="i"
            @click="toJump(i)"
          >
            {{ item }}
          </li>
        </ul>
      </div>
      <div class="info_item">
        <div class="item_tile"></div>
        <ul>
          <li class="item_content">
            <img
              class="item_content_icon"
              src="../assets/img/phone.png"
              alt=""
            /><span class="add_name">:</span>
            <div>{{ companyInfo.phone }}</div>
          </li>
          <li class="item_content">
            <img
              class="item_content_icon"
              src="../assets/img/user.png"
              alt=""
            /><span class="add_name">:</span>
            <div>{{ companyInfo.mobilePhone }}</div>
          </li>
          <li class="item_content">
            <img
              class="item_content_icon"
              src="../assets/img/mail.png"
              alt=""
            /><span class="add_name">:</span>
            <div>{{ companyInfo.cellBankUrl }}</div>
          </li>
          <li class="item_content">
            <img
              class="item_content_icon"
              src="../assets/img/address.png"
              alt=""
            /><span class="add_name">:</span>
            <div>{{ companyInfo.contactAddress }}</div>
          </li>
        </ul>
      </div>
    </div>
    <div class="adderss">
      Copyright 2019  mingzhoubio.com All Rights
      Reserved. ICP19018319-1
    </div>
  </div>
</template>
<script>
import { queryCompany } from "/api/user";
export default {
  data() {
    return {
      navList: [
        "",
        "",
        "",
        "",
        "",
        "ATCC",
      ],
      persionInfo: ["", "", "", ""],
      contactList: [
        "010-11111111",
        "",
        "service@mingzhoubio",
        "299122627282930310101820",
      ],
      companyInfo: {},
    };
  },
  methods: {
    toJump(i) {
      let that = this;
      switch (i) {
        case 0:
          that.$router.push({
            path: "/login",
            query: {},
          });
          break;
        case 1:
          that.$router.push({
            path: "/register",
            query: {},
          });
          break;
        case 2:
          this.$router.push({
            path: "/mine/cart",
            query: {},
          });
          break;
        case 3:
          this.$router.push({
            path: "/mine/personal",
            query: {},
          });
          break;
        default:
          that.$router.push({
            path: "/",
            query: {},
          });
          break;
      }
    },
    queryCompany() {
      queryCompany({}).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.companyInfo = res.data;
        }
      });
    },
  },
  mounted() {
    this.queryCompany();
  },
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.footer {
  width: 1440px;
  height: 318px;
  background-color: #ffffff;
  padding: 24px 120px 0;
  margin: 0 auto;
}
.footer_info {
  width: 1200px;
  height: 240px;
  border-bottom: 1px solid #ffffff;
  display: flex;
}
.info_item {
  width: 360px;
}
.item_tile {
  color: #000000;
  font-size: 16px;
  color: #000000;
  line-height: 22px;
  height: 22px;
  margin-bottom: 13px;
  font-weight: bold;
}
.item_content {
  font-size: 12px;
  color: #444444;
  line-height: 22px;
  display: flex;
  cursor: pointer;
}
.adderss {
  margin-top: 30px;
  border-top: 1px solid #a4a4a4;
  height: 58px;
  line-height: 58px;
  font-size: 12px;
  color: #444444;
}
.item_content_icon {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  position: relative;
  top: 2px;
}
.add_name {
  min-width: 48px;
}
</style>



