<template>
  <div class="header-box">
    <div class="header">
      <div class="header_left">
        <img src="../assets/img/qa.png" class="website_img" />
        <div class="website_name">
          <router-link to="/"></router-link>
        </div>
      </div>
      <div class="header_right">
        <div class="search">
          <input
            class="search_input"
            v-model="searchInfo"
            type="text"
            placeholder=""
          />
          <div class="search_btn" @click="toSearch()">
            <img src="../assets/img/search.png" alt="" />
          </div>
        </div>
        <div class="has_login" v-if="userinfo && userinfo.username">
          <img src="../assets/img/cartb.png" class="cart_img" />
          <span class="shop_cart" @click="toCart()"
            >(<span class="cart_num">{{ 5 }}</span
            >)</span
          >
          <img
            src="../assets/img/user.png"
            class="head_img"
            @click="toMine()"
          />
          <span class="user_name" @click="toMine()"
            >{{ userinfo.username }}&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;</span
          ><span class="login_out" @click="lgout()"></span>
        </div>
        <div class="has_login" v-else>
          <span class="user_name" @click="tologin()"
            >&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;</span
          ><span class="login_out" @click="toregister()"></span>
        </div>
      </div>
    </div>
    <slot name="swiper"></slot>
    <div class="nav">
      <div
        class="nav_item"
        v-for="(item, i) in navList"
        :key="i"
        @mousemove="showBar(i)"
        @mouseleave="hideBar(i)"
      >
        <div class="nav_main" @click="chooseNav(i, item)">
          {{ item.name }}
        </div>
        <div class="nav_b" :class="[i == showi ? 'nav_b_show' : '']">
          <div
            class="nav_b_item"
            v-for="itemi of item.childMenus"
            :key="itemi.id"
            @click="chooseNavBar(itemi.id)"
          >
            {{ itemi.name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
import { getCartList, cartDel, getQuickSearch } from "/api/goods";
import { queryBar, userGet, logout } from "/api/user";
import { loginOut, navList } from "/api/index";
import { setStore, getStore, removeStore } from "/utils/storage";
export default {
  props: {},
  data() {
    return {
      user: {},
      input: "",
      choosePage: -1,
      searchResults: [],
      timeout: null,
      token: "",
      navList: [],
      showi: 99,
      userinfo: {},
      searchInfo: "",
    };
  },
  computed: {
    ...mapState(["cartList", "login", "receiveInCart", "showCart", "userInfo"]),
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    chooseNavBar(id) {
      let that = this;
      switch (id) {
        case 15:
          that.$router.push({
            path: "/scientific/support",
            query: {},
          });
          break;
        case 16:
          that.$router.push({
            path: "/scientific/about",
            query: {},
          });
          break;
        default:
          that.$router.push({
            path: "/",
            query: {},
          });
          break;
      }
    },
    chooseNav(i, item) {
      let that = this;
      switch (i) {
        case 0:
          that.$router.push({
            path: "/product",
            query: {},
          });
          break;
        case 1:
          that.$router.push({
            path: "/platform",
            query: {},
          });
          break;
        case 2:
          that.$router.push({
            path: "/scientific",
            query: {},
          });
          break;
        case 3:
          that.$router.push({
            path: "/scientific/standard",
            query: {},
          });
          break;
        case 4:
          that.$router.push({
            path: "/scientific/support",
            query: {},
          });
          break;
        case 5:
          that.$router.push({
            path: "/scientific/union",
            query: {},
          });
          break;
        default:
          that.$router.push({
            path: "/",
            query: {},
          });
          break;
      }
      console.log(i, item);
    },
    toSearch() {
      this.$router.push({
        path: "/scientific/search",
        query: {
          searchInfo: this.searchInfo,
        },
      });
    },
    toMine() {
      this.$router.push({
        path: "/mine/personal",
        query: {},
      });
    },
    toCart() {
      this.$router.push({
        path: "/mine/cart",
      });
    },
    tologin() {
      this.$router.push({
        path: "/login",
      });
    },
    toregister() {
      this.$router.push({
        path: "/register",
      });
    },
    lgout() {
      logout({}).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.userGet();
          this.$router.push({
            path: "/home",
            query: {},
          });
        }
      });
    },
    ...mapMutations([
      "ADD_CART",
      "INIT_BUYCART",
      "ADD_ANIMATION",
      "SHOW_CART",
      "REDUCE_CART",
      "RECORD_USERINFO",
      "EDIT_CART",
    ]),
    queryBar() {
      queryBar({}).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.navList = res.data;
        }
      });
    },
    userGet() {
      userGet({}).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.userinfo = res.data;
        } else {
          this.userinfo = {};
        }
      });
    },
    showBar(i) {
      this.showi = i;
    },
    hideBar(i) {
      this.showi = 99;
    },
  },
  mounted() {
    this.queryBar();
    this.userGet();
  },
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
// @import "../assets/style/theme";
// @import "../assets/style/mixin";

.header-box {
  background: #ffffff;
  // background-image: -webkit-linear-gradient(#000, #121212);
  // background-image: linear-gradient(#000, #121212);
  width: 1440px;
  margin: 0 auto;
}
.header {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  background: #ffffff;
}
.header_left,
.header_right {
  display: flex;
}
.cart_img,
.head_img {
  width: 16px;
  height: 16px;
  position: relative;
  top: 0;
  margin-right: 6px;
  cursor: pointer;
}
.search {
  height: 36px;
  margin-top: 32px;
  background: #ffffff;
  margin-right: 30px;
  display: flex;
  .search_input {
    border-radius: 4px 0 0 4px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding-left: 12px;
    height: 36px;
    line-height: 36px;
    width: 157px;
  }
  .search_btn {
    width: 43px;
    height: 36px;
    background: #0695d4;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    img {
      width: 20px;
      height: 20px;
      position: relative;
      top: 8px;
      left: 11px;
    }
  }
}
.website_img {
  width: 36px;
  height: 36px;
  margin: 32px 9px 32px 134px;
}
.website_name {
  font-size: 24px;
  line-height: 100px;
  height: 100px;
  font-weight: bold;
  color: #1d86ea;
}
.header_right {
  justify-content: end;
  display: flex;
}
.login_out {
  font-size: 16px;
  color: #333333;
  line-height: 100px;
  margin-right: 120px;
  width: 40px;
  cursor: pointer;
}
.user_name {
  font-size: 16px;
  color: #333333;
  line-height: 100px;
  cursor: pointer;
}
.shop_cart {
  font-size: 16px;
  color: #333333;
  line-height: 100px;
  margin-right: 30px;
  cursor: pointer;
}
.cart_num {
  color: #0695d4;
  font-size: 18px;
  font-weight: bold;
}
.input-with-select {
  margin: 30px;
  width: 200px;
  height: 36px;
}
.nav {
  width: 1440px;
  height: 64px;
  background-color: #0593d2;
  padding: 0 140px;
  margin-top: 0 auto;
  display: flex;
  .nav_item {
    height: 64px;
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    cursor: pointer;
    position: relative;
    z-index: 99;
    .nav_main {
      position: relative;
      z-index: 99;
      padding: 21px 18px;
      line-height: 22px;
    }
    .nav_b {
      position: absolute;
      z-index: 99;
      background: #ffffff;
      width: 200px;
      display: none;
      font-size: 16px;
      color: #333333;
      font-weight: bold;
      border: 1px solid #efefef;
      .nav_b_item {
        line-height: 58px;
        padding-left: 15px;
        text-align: left;
      }
      .nav_b_item:hover {
        background: #d0f0ff;
      }
    }
    .nav_b_show {
      display: block;
    }
  }
  .nav_item:hover {
    background: #00577c;
  }
}
// .nav_item{
// 	width: 150px;
// 	text-align: center;
// 	height: 100%;
// 	font-size: 18px;
// 	color: #FFFFFF;
// }
// .nav_item:hover{
// 	background: #fff;
// 	color: #000;
// }
.el-dropdown {
  width: 150px;
  text-align: center;
  height: 100%;
  font-size: 18px;
  color: #ffffff;
}
.el-dropdown:hover {
  background: #00577c;
}
.el-dropdown-menu__item {
  width: 150px;
  text-align: center;
}
.el-dropdown-menu {
  margin: 0;
  padding: 0;
}
.el-dropdown-menu__item--divided {
  margin-top: 0;
}
</style>




